.app-header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    .logo{
        font-size: 30px;
        width: 84px;
    }
}
.out-content{
    list-style: none;
    display: flex;
    color: #fff;
    font-size: 18px;
    padding: 0px 40px 0px 10px;
    .out-item{
        line-height: 22px;
        cursor: pointer;
        &:hover{
            color: #00ED89;
        }
    }
}
